    .learning-filter {
      order: -1;
    }
    .artist-table {
      .appointment-table.customer-table {
            table {
                tr {
                      td {
                          &:last-child {
                                min-width: 100px;
                          }
                      }
                }
            }
      }
    }
    &.dark-only {
      .page-wrapper {
        &.horizontal-wrapper {
          .page-body-wrapper {
            .sidebar-wrapper {
              .sidebar-main {
                #sidebar-menu {
                  background-color: $dark-body-background;
                }
              }
            }
          }
        }
      }
    }
    .email-wrap {
